<template>
  <div class="list row">
    <div>search</div>
    <div>List
      <button class="m-3 btn btn-sm btn-danger" @click="removeAll">Delete All</button>
    </div>
  </div>
</template>

<script>
import VueProjectService from "@/service/VueProjectService";
export default {
  name: "vueProject-list",
  data() {
    return {
      title: "",
      vueProjectList: [],
      currentVueProject: null,
      currentIndex: -1
    }
  },
  methods: {
    findAll() {
      VueProjectService.getAll()
      .then(response => {
        this.vueProjectList = response.data;
      })
      .catch(e => {
        console.log(e);
      })
    },
    refreshList() {
      this.findAll()
      this.currentIndex = -1
      this.currentVueProject = null
    },
    removeAll() {
      VueProjectService.deleteAll()
      .then(response => {
        console.log(response.data)
        this.refreshList();
      })
      .catch(e => {
        console.log(e);
      })
    }
  },
  mounted() {
    this.findAll();
  }
}
</script>

