import http from './http-common';

class VueProjectService {
    getAll() {
        return http.get(`/findAll`)
    }
    get(id) {
        return http.get(`/findById/${id}`)
    }
    create(data) {
        return http.post(`/add`, data)
    }
    update(id, data) {
        return http.put(`/update/${id}`, data)
    }
    delete(id) {
        return http.delete(`deleteById/${id}`)
    }
    deleteAll() {
        return http.delete(`/deleteAll`)
    }
    findByTitle(keyword) {
        return http.get(`/findAll?keyword=${keyword}`)
    }
}

export default VueProjectService();